a {
  text-decoration: none;
}

a:visited {
  text-decoration: none;
  color: white;
}

a:hover {
  text-decoration: none;
  color: white;
}

a:focus {
  text-decoration: none;
  color: white;
}

a:hover, a:active {
  text-decoration: none;
  color: white;
}

body {
  background-color: #37414e;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vW;
  height: auto;
  background-color: #37414e;
}

.header-main-temperature-display {
  padding-left: 10vW;
  font-family: "JetBrains Mono", monospace;
  font-weight: 900;
  color: rgb(230, 230, 230);
  font-size: 8em;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  line-height: 0.5em;
}

.header-main-location-display {
  padding-left: 12vW;
  margin-bottom: 4em;
  font-family: "Reddit Sans", sans-serif;
  font-weight: 300;
  color: rgb(230, 230, 230);
  font-size: 1.25em;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  line-height: 0.5em;
}

.content-detail-container {
  width: 15vW;
  height: 20vW;
  background-color: #37414e;
  filter: brightness(0.9);
  margin: 1em;
  border-radius: 1em;
  display: flex;
  justify-content: center;
}
.content-detail-container:hover {
  filter: brightness(0.85);
  cursor: pointer;
}

.content-detail-text {
  font-family: "JetBrains Mono", monospace;
  font-weight: 800;
  color: rgb(230, 230, 230);
  font-size: 6em;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
}

.content-detail-units {
  font-family: "JetBrains Mono", monospace;
  font-weight: 400;
  color: rgb(230, 230, 230);
  font-size: 1em;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  padding-top: 3em;
}

