// FUNCTIONS AND MIXINS
@mixin userSelect{
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;}
@mixin font($type, $weight:400, $color:black, $size:1em) {
    $fontSerif: "New York", serif;
    $fontSans: "Reddit Sans", sans-serif;
    $fontMono: "JetBrains Mono", monospace;
    @if $type == 'serif' {
        font-family: $fontSerif;
    } @else if $type == 'sans' {
        font-family: $fontSans;
    } @else if $type == 'mono' {
        font-family: $fontMono;
    }
    font-weight: $weight;
    color: $color;
    font-size: $size;}
@mixin selectable {
    &:hover {
        filter: brightness(0.85);
        cursor: pointer;
    }}
@mixin highlight {
    ::selection {
        background: #425C57; /* WebKit/Blink Browsers */
      }
      ::-moz-selection {
        background: #425C57; /* Gecko Browsers */
      }}

$backingColor: #37414e;
$fontColor: rgb(230, 230, 230);

// CLASSES FOR HOMEPAGE
a { text-decoration: none; }
a:visited { text-decoration: none; color:white; }
a:hover { text-decoration: none; color:white; }
a:focus { text-decoration: none; color:white; }
a:hover, a:active { text-decoration: none; color:white }


body{
    background-color: $backingColor;
}

.App {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vW;
    height: auto;
    background-color: $backingColor;
  }


.header-main-temperature-display{
    padding-left: 10vW;
    @include font('mono', 900, $fontColor, 8em);
    @include userSelect();
    line-height: 0.5em;
}

.header-main-location-display{
    padding-left: 12vW;
    margin-bottom: 4em;
    @include font('sans', 300, $fontColor, 1.25em);
    @include userSelect();
    line-height: 0.5em;
}

.content-detail-container{
    width: 15vW;
    height: 20vW;
    background-color: $backingColor;
    filter: brightness(0.90);
    margin: 1em;
    border-radius: 1em;
    display: flex;
    justify-content: center;
    @include selectable();
}

.content-detail-text{
    @include font('mono', 800, $fontColor, 6em);
    @include userSelect();
}

.content-detail-units{
    @include font('mono', 400, $fontColor, 1em);
    @include userSelect();
    padding-top: 3em;
}